import axios from 'axios';
import request from "@/utils/request";

export default class RoleService {

	getRoles() {
		return request({
			url: 'http://localhost:10000/role/all',
			method: 'get',
		})
    }

	saveRole(data) {
		return request({
			url: 'http://localhost:10000/role/save',
			method: 'post',
			data
		})
	}
	deleteRole(data) {
		return request({
			url: 'http://localhost:10000/role/delete',
			method: 'post',
			data
		})
	}
	deleteRoleBatch(data) {
		return request({
			url: 'http://localhost:10000/role/deleteBatch',
			method: 'post',
			data
		})
	}
}
